.sale {
    padding-top: 10px;
    padding-bottom: 20px;

    &-item {
        img {
            position: absolute;
            left: 0;
            top: 0;
        }

        &_light {

            .homesale-text h3,
            p {
                color: #fff;
            }
        }
    }

    &-filter {
        margin-bottom: 40px;
        display: flex;
        gap: 0 20px;

        &__item {
            max-width: 285px;
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            position: relative;

            &::before {
                content: '';
                position: absolute;
                bottom: -8px;
                left: 0;
                width: 100%;
                height: 1px;
                background: #E9E9EC;
            }

            &-name {
                font-weight: 600;
                color: #1F1A3D;
                font-size: 18px;
                line-height: 22px;
            }
        }
    }

    &__title {
        color: #000;
        padding-left: 15px;
        font-weight: 600;
        font-size: 24px;
        line-height: 29px;
        margin: 0 0 40px;
    }
}










@media (max-width: 1024px) {
    .sale__title {
        padding-left: 0;
        margin-bottom: 25px;
    }
    .sale-filter {
        flex-direction: column;
        gap: 20px 0;
        margin-bottom: 25px;

        &__item {
            max-width: 100%;

            &-name {
                font-size: 14px;
                line-height: 17px;
            }
        }
    }
}

@media (max-width: 520px) {
    .sale-wrapper {
        display: block;
    }
}

@media (min-width: 320px) and (max-width: 640px) {
    .sale__title {
        margin-bottom: 15px;
    }
}