@import "node_modules/bootstrap/scss/bootstrap-reboot.scss"; 


@font-face { src: url("../fonts/Montserrat/Montserrat-Regular.ttf") format("truetype"); font-family: "Montserrat"; font-weight: 400;}
@font-face { src: url("../fonts/Montserrat/Montserrat-SemiBold.ttf") format("truetype"); font-family: "Montserrat"; font-weight: 600;}
@font-face { src: url("../fonts/Montserrat/Montserrat-Bold.ttf") format("truetype"); font-family: "Montserrat"; font-weight: 700;}
@font-face { src: url("../fonts/Montserrat/Montserrat-Medium.ttf") format("truetype"); font-family: "Montserrat"; font-weight: 500;}



:root {
	--accent: orange;
	--text: #7F8B89;
	--regular-text: 16px;
	--lineheight: 1.65;
	--userfont: "Montserrat", OpenSans;
	--systemfont: "Montserrat", OpenSans, Arial, sans-serif;
}
  
::placeholder { color: #666; }
::selection { background-color: var(--accent); color: #fff; }
input, textarea { outline: none; }
input:focus:required:invalid, textarea:focus:required:invalid { border-color: red; }
input:required:valid, textarea:required:valid { border-color: green; }

body {
	font-family: var(--userfont);
	font-size: var(--regular-text);
	line-height: var(--lineheight);
	color: var(--text);
	min-width: 320px;
	position: relative;
	overflow-x: hidden;
}

html, body {
	height: 100%;
}

.wrapper {
	display: flex;
	flex-direction: column;
	min-height: 100%;
}
.main {
	flex: 1 0 auto;
}
.footer {
	flex: 0 0 auto;
}


.container {
	max-width: 1296px;
	margin: 0 auto;
	padding: 0 43px;
	@media (max-width: 1024px) {
		max-width: 640px;
		padding: 0 20px;
	}
	@media (max-width: 600px) {
		padding: 0 15px;
		min-width: 320px;
	}
}

.header-top {
	padding: 8px 0;
	border-bottom: 1px solid #E9E9EC;
}

.header-wrap {
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: relative;
}

.header-select {
	min-width: 106px;
}

.header-select .choices__list--dropdown .choices__item {
	padding: 8px;
}

.header-select .choices__list--dropdown .choices__item--selectable {
	padding-right: 8px;
}

.select-img__ru {
	width: 28px;
	height: 20px;
	background-image: url('../img/ru-lang.svg');
	display: flex;
	font-size: 30px;
}

.header-select .choices__list--single .choices__item {
	width: 100%;
	display: flex;
	align-items: center;
	font-size: 14px;
	line-height: 17px;
	color: #1F1A3D;
}

.header-select .select-img {
	width: 28px;
	height: 22px;
	margin-right: 12px;
}

.header-left,
.header-topmenu,
.header-list ul,
.header-topmenu ul {
	display: flex;
	align-items: center;
}

.header-list ul {
	margin: 0 0 0 16px;
	padding: 0;
	li {
		list-style-type: none;
		a {
			font-size: 14px;
			line-height: 17px;
			color: #8F8C9E;
			text-decoration: none;
		}
	}
}

.header__tel {
	font-size: 14px;
	line-height: 17px;
	color: #1F1A3D;
	text-decoration: none;
	margin: 0 0 0 16px;
	padding: 0;
	display: flex;
	align-items: center;
	img {
		margin-right: 11px;
	}
}

.header-topmenu ul {
	margin: 0;
	padding: 0;
	li {
		list-style-type: none;
		a {
			font-size: 14px;
			line-height: 17px;
			color: #8F8C9E;
			text-decoration: none;
		}
		& + li {
			margin-left: 32px;
		}
	}
}

.header-select .choices__inner {
	padding: inherit;
	min-height: inherit;
	border-radius: 0px;
	border: 1px solid #E9E9EC;
}

.header-select .choices__list--single {
	display: inline-block;
	padding: 8px 16px 8px 8px;
	width: 100%;
}

.header-select .choices[data-type*=select-one] .choices__inner {
	padding-bottom: 0;
	background-color: #FFFFFF;
}

.header-middle {
	padding: 16px 0;
	border-bottom: 1px solid #E9E9EC;
}

.menuwrap {
	background: #1F1A3D;
	padding: 14px 16px;
	min-width: 122px;
	font-size: 16px;
	line-height: 20px;
	display: flex;
	align-items: center;
	color: #FFFFFF;
	margin-right: 32px;
	cursor: pointer;
	position: relative;
}

.product-item__url {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
}

.catalog-select .choices__inner {
	display: inline-block;
	vertical-align: top;
	width: 100%;
	background-color: #ffffff;
	padding: 8px;
	border: 1px solid #E9E9EC;
	border-radius: 2.5px;
	font-size: 14px;
	min-height: 33px;
}

.catalog-select .choices__list--single .choices__item {
	width: 100%;
	font-weight: 400;
	font-size: 14px;
	line-height: 17px;
	display: flex;
	align-items: center;
	letter-spacing: -0.015em;
	color: #000000;
}

.catalog-select .choices__list--single {
	display: inline-block;
	padding: 0 16px 0 0;
	width: 100%;
}

.choices.is-open {
	position: relative;
	z-index: 999;
	}

.catalog-title {
	padding: 12px 0 40px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	.choices__list--dropdown .choices__item--selectable {
		padding-right: 0px!important;
	}
	h1 {
		font-style: normal;
		font-weight: 600;
		font-size: 30px;
		line-height: 37px;
		color: #000000;
	}
} 

.catalog-column {
	display: flex;
}

.catalog-left {
	width: 224px;
	flex-shrink: 0;
	margin-right: 20px;
}

.catalog-wrapper {
	display: flex;
	flex-wrap: wrap;
}

.catalog-item {
	width: calc(25% - 20px);
	margin: 0px 10px 32px;
}

.catalog-right {
	width: 100%;
}

.catalog-menu ul {
	margin: 0;
	padding: 0;
	li {
		list-style-type: none;
		width: 100%;
		a {
			font-style: normal;
			font-weight: 600;
			font-size: 18px;
			line-height: 32px;
			color: #1F1A3D;
			text-decoration: none;
			padding: 4px 16px;
			transition: all .3s ease;
			display: block;
			&:hover {
				background-color: #1F1A3D;
				color: #ffffff;
			}
		}
	}
}

.filter-check {
	margin: 32px 0;
}

.filter-check h3 {
	font-style: normal;
	font-weight: 700;
	font-size: 16px;
	line-height: 20px;
	color: #000000;
	padding-bottom: 16px;
	border-bottom: 1px solid #E9E9EC;
	margin-bottom: 0;
}

.header-search {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: space-between;
	max-width: 286px;
	width: 100%;
	height: 32px;
	background: #FFFFFF;
	border: 1px solid #E9E9EC;
	margin-left: 32px;
	margin-right: 20px;
}

.search-input {
	outline: none;
	font-size: 12px;
	line-height: 15px;
	color: #8F8C9E;
	border: none;
	padding: 7.5px 0px 7.5px 16px;
	width: 100%;
	&::placeholder {
		color: #8F8C9E;
	}
}

.close-search.active {
	width: 24px;
	height: 24px;
	position: relative;
	cursor: pointer;
	margin-right: 10px;
}

.close-search.active::before {
	position: absolute;
	content: '';
	width: 24px;
	height: 1px;
	z-index: 10;
	transform: rotate(45deg);
	top: 0;
	bottom: 0;
	margin: auto;
	left: 0;
	right: 0;
	background-color: #000000;
}

.close-search.active::after {
	position: absolute;
	content: '';
	width: 24px;
	height: 1px;
	z-index: 10;
	transform: rotate(135deg);
	top: 0;
	bottom: 0;
	margin: auto;
	left: 0;
	right: 0;
	background-color: #000000;
}

.close-search {
	display: none;
}

.close-search.active {
	display: block;
}

input[type="search"]::-ms-clear {
	width: 20px;
	height: 20px;
	background-image: url(../img/close.svg);
}


.search-button {
	background: #FFFFFF;
	border: none;
	width: 24px;
	height: 24px;
	margin-right: 16px;
	padding: 0;
	display: flex;
	align-items: center;
}

.header-card__login,
.header-card__card {
	font-size: 16px;
	line-height: 20px;
	color: #1F1A3D;
	text-decoration: none;
	display: flex;
	align-items: center;
	span {
		margin-left: 5px;
	}
}

.header-card__login img {
	margin-right: 20px;
}

.header-card__card img {
	margin-right: 20px;
}

.header-card {
	display: flex;
	align-items: center;
}

.header-card a + a {
	margin-left: 20px;
}

.header-bottom {
	padding: 16px;
	border-bottom: 1px solid #E9E9EC;
}

.header-menuhead ul {
	margin: 0;
	padding: 0;
	display: flex;
	align-items: center;
	justify-content: space-between;
	li {
		list-style-type: none;
		& + li {
			margin-left: 15px;
		}
		a {
			font-size: 16px;
			line-height: 20px;
			color: #1F1A3D;
			text-decoration: none;
		}
	}
}


/* swiper-bullets styling */
.swiper-pagination-bullet {
	width: 10px;
	height: 10px;
	margin: 0 15px;
	background-color: tomato;
	opacity: 1;
}

.swiper-pagination-bullet-active {
	background-color: navy;
}

.swiper-button-prev,
.swiper-button-next {
	position: static;
	margin-left: 10px;
}

.swiper-container {
	padding-bottom: 50px;
}

.slider {
	display: flex;
	flex-direction: row-reverse;
}

.slider-nav {
	overflow: hidden;
	width: 285px;
	margin-left: 20px;
	flex-shrink: 0;
	height: 330px;
}

.slider-main {
	overflow: hidden;
	height: 330px;
}

.slider-nav .swiper-slide {
	height: 155px!important;
	display: flex;
	align-items: center;
	justify-content: center;
	img {
		object-fit: cover;
	}
}

.slider-nav .swiper-slide {
	overflow: hidden;
}

.slider-nav .swiper-slide img {
	object-fit: cover;
}

.slider-banner .swiper-slide {
	display: flex;
	a {
		width: 100%;
	}
}

.swiper-slide img {
	object-fit: cover;
	width: 100%;
	height: 100%;
}

button.swiper-button-prev {
	width: 32px;
	height: 32px;
	background: url(../img/left-slider.svg);
	border: none;
	padding: 0;
	margin: 0;
	top: calc(50% - 16px);
	left: 16px;
}

button.swiper-button-next {
	width: 32px;
	height: 32px;
	background: url(../img/right-slider.svg);
	border: none;
	padding: 0;
	margin: 0;
	top: calc(50% - 16px);
	right: 16px;
}

.slider-main {
	position: relative;
}

button.swiper-button-next:after,
button.swiper-button-prev:after {
	display: none;
}

.slider-container {
	position: relative;
	padding-bottom: 28px;
}

.swiper-pagination {
	position: absolute;
	text-align: center;
	transition: .3s opacity;
	transform: translate3d(0,0,0);
	z-index: 10;
	bottom: 0;
	left: 0;
	right: 0;
	display: flex;
	justify-content: center;
}

.swiper-pagination-bullet {
	width: 12px!important;
	height: 12px!important;
	display: inline-block;
	border-radius: 100%;
	background: #8F8C9E!important;
	opacity: 1!important;
	margin: 0 8px!important;
}

.swiper-pagination-bullet-active {
	opacity: 1;
	background: #1F1A3D!important;
}

.slider-banner {
	margin: 4px 0 32px;
}

.swiper-text {
	overflow: hidden;
}

.swiper-text-p {
	display: inline-flex!important;
	width: auto!important;
	font-size: 14px;
	line-height: 17px;
	color: #FFFFFF;
}

.slider-text {
