.styled-checkbox {
	position: absolute; // take it out of document flow
	opacity: 0; // hide it
  
	& + label {
	  position: relative;
	  cursor: pointer;
	  font-style: normal;
		font-weight: 400;
		font-size: 14px;
		line-height: 17px;
		color: #1F1A3D;
		display: flex;
		align-items: center;
		margin: 16px 0;
	}
  
	// Box.
	& + label:before {
	  content: '';
	  margin-right: 8px;
	  display: inline-block;
	  vertical-align: text-top;
	  width: 24px;
	  height: 24px;
	  border: 1px solid #E9E9EC;
	}
  
	// Box hover
	&:hover + label:before {
		background-image: url(../img/check.svg);
		background-repeat: no-repeat;
		background-position: center;
	}
	
	// Box focus
	&:focus + label:before {
	  box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.12);
	}
  
	// Box checked
	&:checked + label:before {
		background-image: url(../img/check.svg);
		background-repeat: no-repeat;
		background-position: center;
	}
	
	// Disabled state label.
	&:disabled + label {
	  color: #965454;
	  cursor: auto;
	}
  
	// Disabled box.
	&:disabled + label:before {
	  box-shadow: none;
	  background: rgb(138, 77, 77);
	}
}

