.product-slider-main {
	margin-bottom: 32px;
	position: relative;
}

.accordion__control {
	width: 100%;
	background-color: transparent;
	cursor: pointer;
}

.accordion__content {
	max-height: 0;
	opacity: 0;
	overflow: hidden;
	padding: 0 20px;
	will-change: max-height;
	transition: all 0.3s ease-out;
	box-sizing: content-box;
}

.accordion__title {
	font-weight: bold;
	font-size: 1.17em;
}

.accordion__icon {
	transition: transform 0.3s ease-out;
	width: 10px;
	height: 6px;
}

.open .accordion__icon {
	transform: rotate(180deg);
}

.open .accordion__content {
	opacity: 1;
	padding: 8px 0;
}

.accordion {
	width: 100%;
	margin-bottom: 16px;
	list-style-type: none;
}

.accordion__control {
	display: flex;
	align-items: center;
	justify-content: space-between;
	list-style: none;
	padding: 9px 0;
	font-style: normal;
	font-weight: 600;
	font-size: 24px;
	line-height: 29px;
	color: #1F1A3D;
	border-right: none;
	border-bottom: 1px solid #e9e9ec;
	border-top: 0;
	border-radius: 0;
	border-left: 0;
}

.accordion__control::-webkit-details-marker {
	display: none;
}

.accordion__title {
	margin: 0;
}

.accordion__icon {
	position: relative;
	display: inline-block;
}

.accordion__icon::before {
	content: "";
	background-image: url(../img/arrow-checkk.svg);
	width: 10px;
	height: 6px;
	background-position: center;
	transform: rotate(0);
	margin-bottom: 1px;
	position: absolute;
	right: 0;
}


.accordion__content {
	padding: 8px 0;

	p {
		margin-bottom: 0;
	}
}

.accordion[open] .accordion__icon {
	transform: rotate(45deg);
}

.accordion__control:active {
	border-bottom: 1px solid #e9e9ec;
	border-top: 0;
	border-radius: 0;
	border-left: 0;
} 

.pr-title {
