.close-search.active {
  width: 24px;
  height: 24px;
  position: relative;
  cursor: pointer;
  margin-right: 10px;
}

.close-search.active::before {
  position: absolute;
  content: "";
  width: 24px;
  height: 1px;
  z-index: 10;
  transform: rotate(45deg);
  top: 0;
  bottom: 0;
  margin: auto;
  left: 0;
  right: 0;
  background-color: #000000;
}

.close-search.active::after {
  position: absolute;
  content: "";
  width: 24px;
  height: 1px;
  z-index: 10;
  transform: rotate(135deg);
  top: 0;
  bottom: 0;
  margin: auto;
  left: 0;
  right: 0;
  background-color: #000000;
}

