.info {
    padding-bottom: 65px;
    &__wrapper {
        padding-top: 30px;
        display: flex;
        gap: 0 20px;
    }

    &__title {
        height: fit-content;
        font-weight: 600;
        font-size: 30px;
        line-height: 110%;
        color: #1F1A3D;
        position: relative;
        max-width: 285px;
        width: 100%;
        min-width: 285px;

        &::after {
            content: '';
            position: absolute;
            bottom: -9px;
            left: 0;
            background: #E9E9EC;
            width: 100%;
            height: 1px;
        }
    }

    &__descr {
        color: #1F1A3D;

        &-title {
            font-weight: 600;
            font-size: 18px;
            line-height: 24px;
            margin-bottom: 8px;
        }

        &-text {
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
            margin-bottom: 8px;
        }

        &-item {
            margin-top: 32px;
            
        }

        &-list {
            margin-top: 8px;
            list-style: decimal;
            font-size: 14px;
            &-item {
                &::marker {
                    margin-right: 8px;
                }
            }
            &_nn {
                list-style: none;
                .info__descr-list-item {
                    position: relative;
                    &::before {
                        content:"-";
                        position: absolute;
                        left: -1rem;
                        top: 50%;
                        transform: translateY(-50%);
                    }

                }
            }
        }
    }
    &__btn {
        display: block;
        width: 100%;
        background: #1F1A3D;
        text-align: center;
        color: #fff;
        text-decoration: none;
        padding: 13px 0;
        font-size: 14px;
    }
}

.no-margin {
    margin: 0;
}



@media (max-width: 1024px) {
    .info__wrapper {
        flex-direction: column;
        gap: 0;
    }
    .info__title {
        font-size: 24px;
    }
    .info__descr-item:first-child {
        margin-top: 15px;
    }
    .info {
        padding-bottom: 45px;
    }
}

@media (min-width: 320px) and (max-width: 640px) {
    .info__title {
        font-size: 16px;
    }
    .info__descr-title {
        font-size: 14px;
        line-height: 110%;
    }
    .info__descr-text {
        font-size: 12px;
        line-height: 18px;
    }
    .info__descr-list {
        font-size: 12px;
        line-height: 18px;
    }
    .info {
        padding-bottom: 30px;
    }
}