.brands {
    padding: 20px 0 65px;

    &__search {
        max-width: 100%;
        margin: 0 0 30px;
    }

    &__wrapper {
        display: grid;
        grid-template-columns: repeat(12, 1fr);
        gap: 30px 20px;
    }

    &__item {
        grid-column: 3 span;
        display: flex;
        flex-direction: column;
        align-items: center;

        &-image {
            padding: 87px 32px;
            border: 1px solid #E9E9EC;
        }

        &-name {
            margin-top: 15px;
            font-weight: 600;
            font-size: 18px;
            line-height: 22px;
            color: #000;
        }
    }
}

.image {
    height: auto;
    max-width: 100%;
    -o-object-fit: cover;
    object-fit: cover;
}


@media (max-width: 1024px) {
    .brands {
        padding: 15px 0;
    }

    .brands__wrapper {
        gap: 25px 10px;
    }

    .brands {
        .brands__search {
            display: flex;
            margin: 0 0 15px;
        }
    }

    .brands__item {
        grid-column: 4 span;

        &-image {
            padding: 60px 29px;
        }

        &-name {
            font-size: 14px;
            line-height: 17px;
        }
    }
}

@media (min-width: 320px) and (max-width: 640px) {
    .brands {
        padding: 10px 50px;
    }
    .brands__wrapper {
        gap: 30px 20px;
    }

    .brands__item {
        grid-column: 6 span;

        &-image {
            padding: 41px 18px;
        }

        &-name {
            font-size: 12px;
            line-height: 15px;
        }
    }
}