

@media (max-width : 1440px) {

}

@media (max-width : 1280px) {

}

@media (max-width : 1024px) {

    .product-row, .product-slider {
        width: 100%;
        position: relative;
        overflow: hidden;
    }

    .product-wrapper {
        flex-direction: column;
    }

    .header-card__login {
        font-size: 0;
    }
    
    .header-card__card {
        font-size: 0;
    }

    .header-top {
        display: none;
    }
    .menuwrap {
        padding: 8px;
    }
    .menuwrap-icon {
        margin-right: 4px;
    }
    .menuwrap {
        min-width: auto;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
    }
    .header-logo {
        position: absolute;
        max-width: 261px;
        margin: auto;
        left: 0;
        right: 0;
        img {
            max-width: 100%;
        }
    }
    .header-search {
        display: none;
    }
    .header-card__card img, 
    .header-card__login img {
        margin-right: 0px;
        height: 20px;
    }
    .header-card a+a {
        margin-left: 18px;
    }
    .header-left {
        width: 100%;
    }
    .header-bottom {
        display: none;
    }
    .slider-nav {
        overflow: hidden;
        width: 138px;
        margin-left: 20px;
        flex-shrink: 0;
        height: 253px;
    }
    .slider-main {
        height: 253px;
    }
    .slider-nav .swiper-slide {
        height: 79px!important;
    }
    .swiper-pagination-bullet {
        width: 8px!important;
        height: 8px!important;
    }

    .slider-button {
        display: block;
        margin-top: 16px;
        ul {
            display: flex;
            margin: 0;
            padding: 0;
            flex-wrap: wrap;
            justify-content: space-between;
            li {
                list-style-type: none;
                list-style-type: none;
                border: 1px solid #E9E9EC;
                padding: 16px 8px;
                margin-bottom: 8px;
                width: calc(50% - 10px);
                display: flex;
                justify-content: flex-start;
                align-items: center;
                a {
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 20px;
                    color: #000000;
                    text-decoration: none;
                    img {
                        margin-right: 16px;
                    }
                }
            }
        }
    }

    .slider-banner {
        margin: 4px 0 8px;
    }

    .product-wrapper, .titlewrap {
        margin: 16px 0;
    }
    .titlewrap h2 {
        font-weight: 600;
        font-size: 24px;
        line-height: 29px;
    }
    .product-item-img {
        height: 186px;
    }
    .product-item-text h3 {
        font-size: 14px;
        line-height: 18px;
    }
    .product-item-text p {
        font-size: 14px;
        line-height: 18px;
    }
    .price__origin {
        font-weight: 700;
        font-size: 24px;
        line-height: 29px;
    }
    .homesale-item {
        height: 148px;
        padding: 8px 16px;
    }

    .homesale-text h3 {
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
    }
    .homesale-text p {
        font-size: 15px;
        line-height: 20px;
    }
        
    .logo-item {
        padding: 57px 28px;
    }
    .title-insta h4 {
        font-weight: 600;
        font-size: 18px;
        line-height: 22px;
        margin-bottom: 8px;
    }
    .title-insta p {
        font-weight: 400;
        font-size: 18px;
        line-height: 22px;
        margin-bottom: 8px;
    }
    .title-insta a {
        font-weight: 600;
        font-size: 18px;
        line-height: 22px;
        margin-bottom: 8px;
    }

    .footer-column {
        flex-grow: 1;
        flex-basis: calc(50% - 20px);
        box-sizing: border-box;
        margin: 10px;
        margin-bottom: 30px;
    }
    .footer-pod {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: column;
    }

    .footer-pod a {
        margin-bottom: 8px;
    }
    .footer-pod p {
        margin-bottom: 8px;
    }
    .footer-select {
        margin-top: 16px;
    }

    .product-nav-prev-insta,
    .product-nav-next-insta {
        display: none!important;
    }

    .product-nav-prev-five,
    .product-nav-next-five {
        display: none!important;
    }

    .banner-nav-prev,
    .banner-nav-next {
        display: none!important;
    }

    button.swiper-button-prev {
        width: 32px;
        height: 32px;
        background: url(../img/left-slider.svg);
        border: none;
        padding: 0;
        margin: 0;
        top: calc(24% - 16px);
    }

    button.swiper-button-next {
        width: 32px;
        height: 32px;
        background: url(../img/right-slider.svg);
        border: none;
        padding: 0;
        margin: 0;
        top: calc(24% - 16px);
    }
    .product-nav-prev,
    .product-nav-prev-two,
    .product-nav-prev-three,
    .product-nav-prev-four {
        left: 20px!important;
    }
    .product-nav-next,
    .product-nav-next-two,
    .product-nav-next-three,
    .product-nav-next-four {
        right: 20px!important;
    }

    .product-item-topline {
        max-width: 20px;
    }

    .mobvers {
        display: flex!important;
    }

    .swiper-product {
        padding-bottom: 24px;
    }

    .catalog-left {
        display: none;
    }

    .catalog-item {
        width: calc(33.3% - 20px);
        margin: 0 10px 32px;
    }

    .catalog-navigation__nav {
        width: 100%;
    }

    .modern-page-prev,
    .modern-page-next {
        width: 28px!important;
        height: 28px!important;
    }

    .catalog-select .choices__list--single .choices__item {
        width: 100%;
        font-weight: 400;
        font-size: 12px!important;
        line-height: 15px;
        display: flex;
        align-items: center;
        letter-spacing: -.015em;
        color: #000;
    }

    .catalog-select .choices__inner {
        height: 33px;
        display: flex;
        align-items: center;
        width: 134px;
    }
  

  

}

@media (max-width : 600px) {

    .catalog-item {
        width: calc(50% - 20px);
        margin: 0 10px 32px;
    }

    .slider-nav {
        display: none;
    }

    .slider-main {
        height: 162px;
    }

    .swiper-pagination-bullet {
        width: 4px!important;
        height: 4px!important;
    }

    .swiper-pagination-bullet {
        margin: 0 2px!important;
    }
    .slider-container {
        position: relative;
        padding-bottom: 12px;
    }
    .slider-button {
        display: block;
        margin-top: 8px;
    }
    .slider-button ul li a {
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #000;
        text-decoration: none;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }
    .slider-button ul li a img {
        margin-right: 16px;
        margin-bottom: 5px;
    }
    .swiper-text-p {
        display: inline-flex!important;
        width: auto!important;
        font-size: 12px;
        line-height: 15px;
        color: #fff;
    }
    .slider-text {
        padding: 8px 0;
    }

    .header-logo__desk {
        display: none;
    }

    .header-logo__mob {
        display: flex;
        margin: auto;
    }

    .catalog-title h1 {
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        color: #000;
    }

    .catalog-title {
        flex-direction: column;
        align-items: flex-start;
    }

    .catalog-select {
        width: 100%;
    }

    .catalog__btn {
        width: calc(50% - 10px);
    }

    .catalog-select .choices__inner {
        width: 100%;
    }

}



