@media (min-width : 600px) {

    .header-logo__desk {
        display: flex;
        margin: auto;
    }
    .header-logo__mob {
        display: none;
    }

}

@media (min-width : 1024px) {

    .catalog__btn {
        display: none;
    }

    .slider-button {
        display: none;
    }

    .mobvers {
        display: none!important;
    }

    

    

}

